




















import Vue from "vue";
export default Vue.extend({
  data() {
    return {
      menuOpen: false
    };
  },
  methods: {
    openMenu() {
      this.menuOpen = true;
    },
    closeMenu() {
      this.menuOpen = false;
    },
    goTo(key: string) {
      this.$router.push(key)
      this.closeMenu()
    },
  }
});
