

















import Vue from "vue";
import Modal from "./Modal.vue";
// @ts-ignore
import categories from "./challenges.ts";
export default Vue.extend({
  components: {
    Modal
  },
  data() {
    return {
      categories,
      modalOpen: false,
      selected: {}
    };
  },
  methods: {
    openModal(challenge) {
      this.selected = challenge;
      this.modalOpen = true;
    }
  }
});
