




































import Vue from 'vue'
import { routes } from '@/router'
export default Vue.extend({
  name: 'navbar',
  created() {
    this.activeKey = this.$router.currentRoute.path
  },
  data() {
    return {
      activeKey: '',
    }
  },
  methods: {
    goTo(key: string) {
      this.activeKey = key
      this.$router.push(key)
    },
    isActive(key) {
      return this.activeKey === key
    },
  }
})
